class Home extends Component {
  constructor(props) {
    super(props)
    this.state = {
      typeList: []
    }
  }
  componentDidMount() {
    // console.log('props', queryString)
    // console.log(queryString.parse(window.location.search))
    methods.getPageQuery(query => this.fnPageInit(query))
  }

  fnPageInit(query) {
    this.setState({
      typeList: setData()
    });

    // methods.request({
    //   url: 'https://api.fanyi.baidu.com/api/trans/vip/translate',
    //   data: {
    //     q: "",
    //     from: 'zh',
    //     to: 'en',
    //   },
    // })
  }

  // clickHandle() {
  //   if (methods.isMiniprogram) {
  //     wx.navigateTo({
  //       url: '../typelist/index?type=1'
  //     })
  //   } else {
  //     location.href = 'typelist.html?type=1'
  //   }
  // }

  clickItem(type) {
    methods.goLink('typelist', `?type=${type}`)
  }

  render() {
    let { typeList } = this.state
    // console.log(typeList)
    return (
      <Wraper title="" classN="page_home" loadend={this.fnWraperLoaded}>
        {/* <div onClick={this.clickHandle}></div> */}
        <ul className="type_list">
          {typeList &&
            typeList.length > 0 &&
            typeList.map((item, i) => {
              return (
                item.isShow && <li
                  key={i}
                  className="type_item"
                  onClick={() => this.clickItem(item.type)}
                >
                  <div className="lebal">{item.name}</div>
                  <div className="img_box">
                    <img className="img" src={item.img} mode="widthFix" />
                  </div>
                </li>
              )
            })}
        </ul>
      </Wraper>
    )
  }
}

